<template>
    <div class="out" :style="{marginLeft:theleft,minWidth: '1144px'}">
        <the-sel></the-sel>

        <div class="picture">
            <p style="margin-right: -15px;margin-bottom:60px;">
                <span class="left lefttips">trend</span>
                <el-date-picker
                    v-model="value1"
                    type="date"
                    placeholder="选择日期" style="font-size:16px;margin-right:10px;" class="right cur righttype">
                </el-date-picker>
                <span style="margin-left:40px;margin-right:14px;font-size:14px;lineHeight:30px;"  class="right cur righttype">time</span>
            </p>
            <div id="trend" style="min-width:400px;height:400px"></div>
        </div> 

        <div class="picture">
            <p style="margin-right: -15px;margin-bottom:60px;">
                <span class="left lefttips">active area</span>
                <el-date-picker
                    v-model="value2"
                    type="date"
                    placeholder="选择日期" style="font-size:16px;margin-right:10px;" class="right cur righttype">
                </el-date-picker>
                <span style="margin-left:40px;margin-right:14px;font-size:14px;lineHeight:30px;"  class="right cur righttype">time</span>
            </p>
            <div id="activearea" style="min-width:400px;height:400px"></div>
        </div> 

        <div class="picture">
            <p style="margin-right: -15px;margin-bottom:60px;">
                <span class="left lefttips">active time</span>
            </p>
            <div id="activetime" style="min-width:400px;height:400px"></div>
        </div> 
    </div>
</template>

<script>
import theSel from '../components/type_money'
export default {
    components:{
               theSel
            },
    data(){
        return{
            theleft: "280px",
            value1:'',
            value2:''
        }
    },
    created(){
        
    },
    mounted(){
        this.laychart()
    },
    computed: {
    //监听时间和币种的改变
    themoney() {
      return this.$store.state.moneyty;
    },
    inleft() {
      return this.$store.state.themenuflag;
    },
    thelang() {
      return this.$store.state.alllang;
    }
  },
    watch: {
    themoney(n, o) {
      this.currentPage1 = this.$store.state.yourpage;
      if(this.first){
        this.fornew();
      }
    },
    inleft(n, o) {
      console.log(n);
      this.cglf(n);
      var newchart = setInterval(() => {
         window.activetime.resize();
      }, 100);
      setTimeout(() => {
        clearInterval(newchart);
      }, 1500);
    },
    redrawflag() {
      setTimeout(() => {
        this.initChart(this.xarr, this.drawarr);
      }, 1000);
    },
    thelang() {
      setTimeout(() => {
        this.initChart(this.xarr, this.drawarr);
      }, 1000);
    }
  },
    methods:{
        cglf(n) {
            if (n) {
                this.theleft = "280px";
            } else {
                this.theleft = "100px";
            }
        },
        initChart(arr1, argument,id,windowname) {
        //   var  argument=[{
        //         name: e.word.toUpperCase(),
        //         data: e.arr,
        //         type: "line",
        //         smooth: true
        //     },{
        //         name: e.word.toUpperCase(),
        //         data: e.arr,
        //         type: "line",
        //         smooth: true
        //     },{
        //         name: e.word.toUpperCase(),
        //         data: e.arr,
        //         type: "line",
        //         smooth: true
        //     }]
      var lgarr = []
      var lgselect = {}
      argument.forEach((e,index) => {
        lgarr.push(e.name)
        // if(index >= 3){
        //   lgselect[e.name]=false
        // }
      });
      var colors = ["#4da7fb", "#27d0ab", "#f86677","#f8c366","#b266f8","#666df8","#66f893","#f88166","#66e5f8","#f866ca"]
      var echarts = require('echarts');
      window.windowname = echarts.init(document.getElementById(id));
      var option = {
                tooltip: {
                    trigger: 'axis',
                    textStyle:{
                      align:'left'
                    }
                },
                
                legend: {
                    data:lgarr,
                    // selected:lgselect,
                    bottom:30,
                    itemGap:50
                },
                grid:{
                  bottom:100,
                  top:20,
                  left:100,
                  right:40
                },
                xAxis: {
                    type: 'category',
                    data: arr1,
                    axisLine: {
                        lineStyle: {
                            type: 'solid',
                            color:'#ebecf0',
                            width:'1'
                        }
                    },
                    axisLabel: {
                        textStyle: {
                            color: '#000',//坐标值得具体的颜色
    
                        }
                    }
                },
                yAxis: [
                    {
                        type: 'value',
                        splitLine:{
                          lineStyle:{
                            color:'#ebecf0'
                          }
                        },
                        axisLine: {
                            lineStyle: {
                                type: 'solid',
                                color:'#ebecf0',
                                width:'1'
                            }
                        },
                        axisLabel: {
                            textStyle: {
                                color: '#000',//坐标值得具体的颜色
        
                            }
                        }
                    }
                ],
                
                series: argument,
                color: colors
            };
            window.windowname.setOption(option,true)
            window.onresize = function() {
                // chart.reflow();
                // window.trend.reflow();
                setTimeout(function(){
                window.windowname.resize();
                }, 50)
            };
            },
            //横柱状图
            laychart(){
                var echarts = require('echarts');
                window.activetime = echarts.init(document.getElementById('activetime'));
                var  option = {
                    color: ['#3398DB'],
                    tooltip : {
                        trigger: 'axis',
                        axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                            type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                        }
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    xAxis : [
                        {
                            type : 'value'
                        }
                    ],
                    yAxis : [
                        {
                            type : 'category',
                            data : ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
                            axisTick: {
                                alignWithLabel: true
                            }
                        }
                        
                    ],
                    series : [
                        {
                            name:'直接访问',
                            type:'bar',
                            barWidth: '60%',
                            data:[10, 52, 200, 334, 390, 330, 220]
                        }
                    ]
                };
                window.activetime.setOption(option,true)
                window.onresize = function() {
                    // chart.reflow();
                    // window.trend.reflow();
                    setTimeout(function(){
                    window.activetime.resize();
                    }, 50)
                };
            }
    }        
}
</script>

<style scoped>
.out{
    margin-left: 330px;
    margin-top: 100px;
    padding-left: 1px;
    transition: all 0.5s;
}
.picture {
  width: 100%;
  height: 500px;
  background-color: #fff;
  margin-top: 30px;
  float: right;
  margin-right: 25px;
  padding: 27px 30px 30px 60px;
  box-sizing: border-box;
  box-shadow: 3px 2px 10px 0px rgba(37, 48, 76, 0.08);
}
.left {
  float: left;
}
.lefttips {
  color: #212229;
  font-weight: 600;
  font-size: 16px;
}
.righttype {
  color: #808c9b;
  font-size: 14px;
  line-height: 24px;
  height: 24px;
}
.right {
  float: right;
  margin: 0 15px;
}

</style>
<style>
.picture .el-date-editor.el-input, .el-date-editor.el-input__inner{
    width: 194px;
}
.picture .el-input--suffix .el-input__inner{
    border-radius: 20px;
    background-color: #f7f8fa;
    width: 200px;
}
.picture .el-input__suffix{ 
    right:25px;
}
.picture .el-input__inner{
    font-size:14px;
    color:#797b8e;
    height: 30px;
    line-height: 30px;
}
.picture .el-input__icon{
    line-height: 30px;
}
</style>




